@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600&display=swap);
* {
  margin: 0;
  padding: 0;
}
body {
  margin: 0;
  font-family: 'Poppins', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #a9b7c2;
}

body::-webkit-scrollbar {
  display: none;
}

body {
  -ms-overflow-style: none;
  scrollbar-width: none;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1280px;
}

p {
  line-height: 30px;
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', Courier,
    monospace;
}

h1,
h2 {
  font-family: 'Righteous', cursive;
}

.app {
  display: flex;
  margin: 0 auto;
  max-width: 1280px;
  padding: 0 15px;
  min-height: 100vh;
  border-radius: 10px;
  flex-direction: column;
  background-color: #ffffff;
}

.dark .app {
  display: flex;
  margin: 0 auto;
  max-width: 1280px;
  padding: 0 15px;
  min-height: 100vh;
  border-radius: 10px;
  flex-direction: column;
  background-color: rgb(16, 16, 16);
}

@media (min-width: 800px) {
  .app,
  .dark .app {
    padding: 0 100px;
    margin-top: 5vh;
    min-height: 90vh;
  }
}

.home {
  flex-grow: 1;
  min-width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.home .title {
  width: 100%;
  text-align: center;
}

.home .title p {
  line-height: 50px;
  font-size: 50px;
  font-weight: bolder;
}

.home .title button {
  border: none;
  outline: none;
  cursor: pointer;
  font-size: 24px;
  margin-top: 30px;
  color: #ffffff;
  padding: 10px 15px;
  border-radius: 999px;
  background-color: #3f71f4;
  font-family: 'Righteous', cursive;
}

.home .person {
  width: 100%;
  margin-top: 20px;
}

.home .person img {
  width: 80%;
  display: block;
  margin: 0 auto;
}

@media (min-width: 800px) {
  .home {
    flex-direction: row;
    justify-content: space-between;
  }
  .home .title,
  .home .person {
    width: 50%;
    margin-top: 0;
    text-align: left;
    font-size: 25px;
  }
  .home .person img {
    width: 100%;
  }
}

footer {
    width: 100%;
    padding: 10px 0;
}

footer img {
    max-width: 60px;
    padding-right: 10px;
}

footer img:not(:last-child){
    margin-right: 10px;
}
.about {
    flex-grow: 1;
}

.about h1{
    margin-bottom: 20px;
}
.navbar {
  height: 100%;
}

.nav-wrapper {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.logo {
  display: flex;
  max-width: 130px;
  height: 100%;
}

.logo img {
  width: 100%;
  padding: 10px 0;
  margin-left: -18px;
}

.list-wrapper {
  display: flex;
  align-items: center;
}

.list-wrapper ul {
  top: 0;
  left: -100vw;
  z-index: 3;
  width: 100%;
  height: 86vh;
  padding-top: 140px;
  text-align: center;
  position: absolute;
  transition: all 0.5s ease-in-out;
  background-color: rgba(0, 0, 0, 0.8);
}

.list-wrapper ul li {
  list-style: none;
  margin-bottom: 15px;
}

.list-wrapper ul li a {
  display: block;
  font-size: 24px;
  padding: 10px 0;
  color: #dddddd;
  text-decoration: none;
  transition: all 0.3s ease-in-out;
}

.list-wrapper ul li a:hover {
  color: #000000;
  background-color: #dddddd;
}

.dark .list-wrapper ul li a:hover {
  color: white;
  background-color: #dddddd;
}

.list-wrapper img {
  opacity: 1;
  z-index: 4;
  width: 40px;
  right: 15px;
  padding: 8px;
  position: absolute;
  border-radius: 999px;
  background-color: #dddddd;
  transition: all 0.5s ease-in-out;
}

.switch {
  width: 80px;
  height: 40px;
  margin-top: 10px;
  float: right;
  border-radius: 20px;
  background-color: #778a99;
  position: relative;
}

.flicker {
  width: 30px;
  height: 30px;
  border-radius: 15px;
  background-color: rgb(255, 255, 255);
  position: absolute;
  top: 5px;
  left: 5px;
  box-shadow: inset 4px 4px 4px rgb(255, 255, 255),
    inset -4px -4px 4px rgb(195, 193, 198);
  transition: 0.3s;
}

.dark {
  background-color: #a9b7c2;
  /* background-color: white; */
  color: white;
  transition: 0.3s;
}

.moon {
  width: 20px;
  height: 20px;
  border-radius: 10px;
  box-shadow: 4px 4px 0 0 rgb(255, 255, 255);
  position: absolute;
  top: 5px;
  left: 45px;
}

.dark .flicker {
  transform: translateX(40px);
}

@media (min-width: 800px) {
  .list-wrapper img {
    display: none;
  }

  .list-wrapper ul {
    padding: 0;
    width: 100%;
    height: 100%;
    text-align: left;
    position: relative;
    background-color: transparent;
  }
  .list-wrapper ul li {
    display: inline-block;
    margin-bottom: 0;
  }
  .list-wrapper ul li:not(:last-child) {
    margin-right: 10px;
  }
  .list-wrapper ul li a {
    padding: 5px;
    color: #000000;
  }

  .dark .list-wrapper ul li a {
    padding: 5px;
    color: white;
  }

  .list-wrapper ul li a:hover {
    padding: 5px;
    color: #333333;
    transform: scale(0.8);
    background: transparent;
  }

  .dark .list-wrapper ul li a:hover {
    padding: 5px;
    color: white;
    transform: scale(0.8);
    background: transparent;
  }
}

.skills {
    flex-grow:1;
}

.skills {
    margin-bottom: 20px;
}

.content {
    display: grid;
    font-size: 24px;
    grid-template-columns: 1fr 1fr;
}

.content div {
    margin-bottom: 25px;
}

.content strong {
    line-height: 25px;
    padding-bottom: 15px;
    display: block;
}

.content li {
    margin: 0;
    margin-bottom: 10px;
}

@media (min-width: 800px){
    .content{
        grid-template-columns: 1fr 1fr 1fr;
    }
}
.works {
  flex-grow: 1;
}

.works h1 {
  margin-bottom: 20px;
}
p {
  font-size: large;
}

.contact{
    flex-grow:1;
}

.contact h1{
    margin-bottom: 20px;
}
.contact iframe {
    border: 0;
    width: 100%;
    height: 400px;
    margin-bottom: 15px;
}
